@import "smart-grid.less";

.reset();

.wrapper {
	.wrapper();
}

html, body {
	height: 100vh;
}

body {
	background: #777;
	display: flex;
	flex-direction: column;
	min-height: 650px;
}


.debug {
	display: none;
	.debug(rgba(0, 0, 0, 0.5), 1px solid #ff0);
}

.skip-link {
	position: absolute;
	top: -40px;
	left: 0;
	background: #000000;
	color: white;
	padding: 8px;
	z-index: 100;
}

.skip-link:focus {
	top: 0;
}

.main-content{
	flex-grow: 1;
	flex-shrink: 0;
	flex-basis: auto;
}

.main-content .wrapper{
	display: flex;
	flex-direction: column;
}

header .wrapper{
	display: flex;
	flex-direction: row;
	margin-bottom: 25px;
	audio{
		position: absolute;
	}
	.go_to_home{
		position: absolute;
	}
	h1{
		flex-grow: 1;
		font-size: 24px;
		color: #333333;
		margin-top: 10px;
		text-align: center;
	}
	@media screen and (max-width: 900px) {
		flex-direction: column;
		audio{
			position: relative;
		}
	}
	@media screen and (max-width: 680px) {
		flex-direction: column;
		.go_to_home{
			position: relative;
		}
	}
}

#rotate {
	display: flex;
	justify-content: center;
	max-width: 600px;
	transform-style: preserve-3d;
	margin: 150px auto;
	perspective: 600px;
	transform: rotateX(-15deg);
	.poster {
		position: absolute;
		left: calc(50% - 300px / 2);
		width: 300px;
	}
	.ring {
		transform-style: preserve-3d;
		animation: gallery infinite linear 60s;
	}
	@keyframes gallery {
		0% {
			transform: rotateY(0deg);
		}
		50% {
			transform: rotateY(180deg);
		}
		100% {
			transform: rotateY(360deg);
		}
	}
	.poster1 {
		transform: rotateY(0deg) translateZ(170px);
	}
	.poster2 {
		transform: rotateY(72deg) translateZ(170px);
	}
	.poster3 {
		transform: rotateY(144deg) translateZ(170px);
	}
	.poster4 {
		transform: rotateY(216deg) translateZ(170px);
	}
	.poster5 {
		transform: rotateY(288deg) translateZ(170px);
	}
}

@media only screen and (max-width: 450px) {
	#rotate {
		margin: 50px auto;
		max-width: 500px;
		perspective: 500px;
		.poster {
			left: calc(50% - 265px / 2);
			width: 265px;
		}
		.poster1 {
			transform: rotateY(0deg) translateZ(140px);
		}
		.poster2 {
			transform: rotateY(72deg) translateZ(140px);
		}
		.poster3 {
			transform: rotateY(144deg) translateZ(140px);
		}
		.poster4 {
			transform: rotateY(216deg) translateZ(140px);
		}
		.poster5 {
			transform: rotateY(288deg) translateZ(140px);
		}
	}
}

@media only screen and (max-width: 370px) {
	#rotate {
		margin: 50px auto;
		max-width: 400px;
		perspective: 400px;
		.poster {
			left: calc(50% - 230px / 2);
			width: 230px;
		}
		.poster1 {
			transform: rotateY(0deg) translateZ(115px);
		}
		.poster2 {
			transform: rotateY(72deg) translateZ(115px);
		}
		.poster3 {
			transform: rotateY(144deg) translateZ(115px);
		}
		.poster4 {
			transform: rotateY(216deg) translateZ(115px);
		}
		.poster5 {
			transform: rotateY(288deg) translateZ(115px);
		}
	}
}

@media only screen and (min-width: 767px) {
	.ring:hover {
		animation-play-state: paused;
	}

	.poster:hover img {
		transform: scale(2, 2);
		opacity: 1;
	}
}

@media only screen and (max-width: 767px) {
	.poster img {
		opacity: 0.9;
	}
}

.poster img {
	width: 100%;
	height: 100%;
	opacity: 0.7;
	transition: all 2s;

}

.poster h2{
	color: white;
}

.audio_content {
	margin-top: 5px;
}

/*footer Start*/
footer .wrapper{
	display: flex;
	flex-direction: column;
	font-size: 12px;
	color: #333333;
	padding-bottom: 15px;
}
footer {
	width: 100%;
	bottom: 0;
	text-align: center;
}
/*footer End*/
.page_404{
	display: flex;
	flex-direction: column;
	h2{
		margin-top: 2%;
		margin-left: 5%;
	}
	img{
		margin-left: 15%;
		max-width: 70%;
		height: auto;
	}
}
.go_to_home{
	background-color: #777;
	color: black;
	border: 2px solid #555555;
	padding: 16px 32px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
	margin: 4px 4px 4px 0px;
	-webkit-transition-duration: 0.4s; /* Safari */
	transition-duration: 0.4s;
	cursor: pointer;

}
.go_to_home:hover {
	background-color: #555555;
	color: white;
}

/* New style youtube Start */
.video-responsive {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow:hidden;
	margin-bottom: 10px;
}
.video-responsive iframe,
.video-responsive object,
.video-responsive embed {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border: none;
}
/* New style youtube End */
.video {
	position: relative;
	padding-bottom: 28.125%;
	height: auto;
	overflow:hidden;
	margin-bottom: 10px;
	width: 50%;
}
.video iframe,
.video object,
.video embed {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border: none;
}
