body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1366px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

html,
body {
  height: 100vh;
}

body {
  background: #777;
  display: flex;
  flex-direction: column;
  min-height: 650px;
}

.debug {
  display: none;
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 1366px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.debug > div > div > div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(8.333333333333334% - 30px);
  height: inherit;
  outline: 1px solid #ff0;
  background: rgba(0, 0, 0, 0.5);
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background: #000000;
  color: white;
  padding: 8px;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

.main-content {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: auto;
}

.main-content .wrapper {
  display: flex;
  flex-direction: column;
}

header .wrapper {
  display: flex;
  flex-direction: row;
  margin-bottom: 25px;
}

header .wrapper audio {
  position: absolute;
}

header .wrapper .go_to_home {
  position: absolute;
}

header .wrapper h1 {
  flex-grow: 1;
  font-size: 24px;
  color: #333333;
  margin-top: 10px;
  text-align: center;
}

#rotate {
  display: flex;
  justify-content: center;
  max-width: 600px;
  transform-style: preserve-3d;
  margin: 150px auto;
  perspective: 600px;
  transform: rotateX(-15deg);
}

#rotate .poster {
  position: absolute;
  left: calc(50% - 300px / 2);
  width: 300px;
}

#rotate .ring {
  transform-style: preserve-3d;
  animation: gallery infinite linear 60s;
}

@keyframes gallery {
  0% {
    transform: rotateY(0deg);
  }

  50% {
    transform: rotateY(180deg);
  }

  100% {
    transform: rotateY(360deg);
  }
}

#rotate .poster1 {
  transform: rotateY(0deg) translateZ(170px);
}

#rotate .poster2 {
  transform: rotateY(72deg) translateZ(170px);
}

#rotate .poster3 {
  transform: rotateY(144deg) translateZ(170px);
}

#rotate .poster4 {
  transform: rotateY(216deg) translateZ(170px);
}

#rotate .poster5 {
  transform: rotateY(288deg) translateZ(170px);
}

.poster img {
  width: 100%;
  height: 100%;
  opacity: 0.7;
  transition: all 2s;
}

.poster h2 {
  color: white;
}

.audio_content {
  margin-top: 5px;
}

/*footer Start*/

footer .wrapper {
  display: flex;
  flex-direction: column;
  font-size: 12px;
  color: #333333;
  padding-bottom: 15px;
}

footer {
  width: 100%;
  bottom: 0;
  text-align: center;
}

/*footer End*/

.page_404 {
  display: flex;
  flex-direction: column;
}

.page_404 h2 {
  margin-top: 2%;
  margin-left: 5%;
}

.page_404 img {
  margin-left: 15%;
  max-width: 70%;
  height: auto;
}

.go_to_home {
  background-color: #777;
  color: black;
  border: 2px solid #555555;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 4px 4px 0px;
  -webkit-transition-duration: 0.4s;
  /* Safari */
  transition-duration: 0.4s;
  cursor: pointer;
}

.go_to_home:hover {
  background-color: #555555;
  color: white;
}

/* New style youtube Start */

.video-responsive {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  margin-bottom: 10px;
}

.video-responsive iframe,
.video-responsive object,
.video-responsive embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

/* New style youtube End */

.video {
  position: relative;
  padding-bottom: 28.125%;
  height: auto;
  overflow: hidden;
  margin-bottom: 10px;
  width: 50%;
}

.video iframe,
.video object,
.video embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

